import './VoicePickerModule.css';
import { useState, useEffect, useRef } from 'react';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import MenuItem from '@mui/material/MenuItem';
import SelectMenu from '../SelectMenu/SelectMenu';

export default function VoicePickerModule({ AWS_polly, SubmitVoiceSelectionEngine, SubmitVoiceSelectionID }) {

    // ---------------------------- TEMP ARRAY OF HARDCODED SELECT MENU OPTIONS ----------------------------
    const providers = ['Amazon'];
    const engines = ['standard', 'neural'];
    const genders = ['Female', 'Male'];
    const languages = ['Arabic', 'Danish', 'Dutch', 'English', 'French', 'German', 'Icelandic', 'Italian', 'Japanese', 'Korean', 'Mandarin', 'Norwegian', 'Polish', 'Portuguese', 'Romanian', 'Russian', 'Spanish', 'Swedish', 'Turkish', 'Welsh'];
    // -----------------------------------------------------------------------------------------------


    const [avaliableVoices, GetAvaliableVoices] = useState([]); // all avaliable voices from the selected text to speech provider (amazon, microsoft etc...)


    // ------------------------ SELECT MENU OPTIONS ------------------------
    const [providerOptions, GetProviderOptions] = useState(CreateMenuOptions(providers)); // provider select menu options
    const [engineOptions, GetEngineOptions] = useState(null); // engine select menu options
    const [genderOptions, GetGenderOptions] = useState(null); // gender select menu options
    const [languageOptions, GetLanguageOptions] = useState(null); // language select menu options
    const [accentOptions, GetAccentOptions] = useState(null); // accent select menu options
    const [voiceOptions, GetVoiceOptions] = useState(null); // voice select menu options
    // -----------------------------------------------------------------------


    // ------------------------ CURRENTLY SELECTED MENU OPTION ------------------------
    const [selectedProvider, GetSelectedProvider] = useState(''); // currently selected provider option
    useEffect(() => { RetrieveAvaliableVoices_Ref.current(); RetrieveEngineOptions_Ref.current(); }, [selectedProvider]) // triggers the 'RetrieveAvaliableVoices_Ref' function every time 'selectedProvider' changes

    const [selectedEngine, GetSelectedEngine] = useState(''); // currently selected engine option
    useEffect(() => { RetrieveGenderOptions_Ref.current(); }, [selectedEngine]) // triggers the 'RetrieveGenderOptions_Ref' function every time 'selectedEngine' changes

    const [selectedGender, GetSelectedGender] = useState(''); // currently selected gender option
    useEffect(() => { RetrieveLanguageOptions_Ref.current(); }, [selectedGender]) // triggers the 'RetrieveLanguageOptions_Ref' function every time 'selectedGender' changes

    const [selectedLanguage, GetSelectedLanguage] = useState(''); // currently selected language option
    useEffect(() => { RetrieveAccentOptions_Ref.current(); }, [selectedLanguage]) // triggers the 'RetrieveAccentOptions_Ref' function every time 'selectedLanguage' changes

    const [selectedAccent, GetSelectedAccent] = useState(''); // currently selected accent option
    useEffect(() => { RetrieveVoiceOptions_Ref.current(); }, [selectedAccent]) // triggers the 'RetrieveVoiceOptions_Ref' function every time 'selectedAccent' changes

    const [selectedVoice, GetSelectedVoice] = useState(''); // currently selected voice option
    useEffect(() => { SubmitSelectedVoice_Ref.current(); }, [selectedVoice]) // triggers the 'SubmitSelectedVoice_Ref' function every time 'selectedVoice' changes
    // --------------------------------------------------------------------------------


    const [resetSelect, ResetSelections] = useState([false, false, false, false, false, false]); // the current reset trigger state's of the Select Menu's
    const [selectEnabled, SetSelectionEnables] = useState([true, false, false, false, false, false]); // the current enabled state's of the Select Menu's


    // converts a specified menu options array into 'MenuItem' elements
    function CreateMenuOptions(options) {

        const menuItems = [] // temp array

        // get the key's of each "Menu Item" as string elements of an array, then capitalize the first letter of each string element
        for (var i = 0; i < options.length; i++) { menuItems[i] = <MenuItem key={options[i]} value={i}>{options[i].charAt(0).toUpperCase() + options[i].substring(1)}</MenuItem>; }

        return menuItems; // return the assembled "Menu Item" key string array
    }
    // ---------------------------------------------------------------

    // retrieve the specified 'MenuItem's key name 
    function RetrieveSelectionKey(MenuOptions, selectedOptionValue) {

        if (MenuOptions === null || selectedOptionValue === null) return; // either or parameters not set, end the function here

        if (selectedOptionValue === "") { return "" } // if selected 'Menu Item' has an empty value, return an empty value
        else { return (MenuOptions[selectedOptionValue].key) } // or else turn the value of the selected 'Menu Item'
        
    }
    // ----------------------------------------

    // retrieve all the avaliable voices for the current selected text to speech provider (amazon, microsoft etc...)
    function RetrieveAvaliableVoices() {

        const provider = RetrieveSelectionKey(providerOptions, selectedProvider); // 

        switch (provider) {

            case 'Amazon':

                AWS_polly.describeVoices((err, data) => {

                    if (err) { console.log(err, err.stack); return; }

                    else { GetAvaliableVoices(data.Voices); }

                });

                break;

            default:
                break;
        }
    }
    // -------------------------------------------------------------------------------------------------------------
    // --- a current value reference of the 'RetrieveAvaliableVoices' function ---
    const RetrieveAvaliableVoices_Ref = useRef();
    RetrieveAvaliableVoices_Ref.current = RetrieveAvaliableVoices;
    // ---------------------------------------------------------------------------


    // ------------------------ RETRIEVE THE SELECT MENU OPTIONS ------------------------

    // retrieve the engine menu options
    function RetrieveEngineOptions() {

        if (selectedProvider === '') {
            if (selectEnabled[1] === true) { SetSelectionEnables([true, false, false, false, false, false]) }
        }

        else {
            ResetSelections([resetSelect[0], !resetSelect[1], resetSelect[2], resetSelect[3], resetSelect[4], resetSelect[5]]);
            GetEngineOptions(CreateMenuOptions(engines));
            SetSelectionEnables([true, true, false, false, false, false]);
        }
    }
    // --------------------------------
    // --- a current value reference of the 'RetrieveEngineOptions' function ---
    const RetrieveEngineOptions_Ref = useRef();
    RetrieveEngineOptions_Ref.current = RetrieveEngineOptions;
    // --------------------------------------------------------------------------

    // retrieve the gender menu options
    function RetrieveGenderOptions() {

        if (selectedEngine === '') {

            SubmitVoiceSelectionEngine(''); // if an engine has not been selected, submit an empty variable to the parent component via the 'SubmitVoiceSelectionEngine' hook use state variables

            if (selectEnabled[2] === true) { SetSelectionEnables([true, true, false, false, false, false]) }
        }

        else {

            // submit the selected engine to the parent component via the 'SubmitVoiceSelectionEngine' hook use state function
            SubmitVoiceSelectionEngine(RetrieveSelectionKey(engineOptions, selectedEngine)); // retrieve the selected engine 'MenuItem's key name

            ResetSelections([resetSelect[0], resetSelect[1], !resetSelect[2], resetSelect[3], resetSelect[4], resetSelect[5]]);
            GetGenderOptions(CreateMenuOptions(genders));
            SetSelectionEnables([true, true, true, false, false, false]);
        }
    }
    // --------------------------------
    // --- a current value reference of the 'RetrieveGenderOptions' function ---
    const RetrieveGenderOptions_Ref = useRef();
    RetrieveGenderOptions_Ref.current = RetrieveGenderOptions;
    // -------------------------------------------------------------------------

    // retrieve the language menu options
    function RetrieveLanguageOptions() {

        if (selectedGender === '') {
            if (selectEnabled[3] === true) { SetSelectionEnables([true, true, true, false, false, false]) }
        }

        else {
            ResetSelections([resetSelect[0], resetSelect[1], resetSelect[2], !resetSelect[3], resetSelect[4], resetSelect[5]]);

            const engine = RetrieveSelectionKey(engineOptions, selectedEngine);
            const gender = RetrieveSelectionKey(genderOptions, selectedGender);

            const foundLanguages = [] // temp array

            for (var i = 0; i < avaliableVoices.length; i++) {

                if (avaliableVoices[i].SupportedEngines.includes(engine) === false || avaliableVoices[i].Gender !== gender) continue;

                for (var n = 0; n < languages.length; n++) {

                    if (avaliableVoices[i].LanguageName.includes(languages[n]) === true && foundLanguages.includes(languages[n]) === false) { foundLanguages.push(languages[n]); continue; }
                }
            }

            GetLanguageOptions(CreateMenuOptions(foundLanguages));

            SetSelectionEnables([true, true, true, true, false, false]);
        }
    }
    // ----------------------------------
    // --- a current value reference of the 'RetrieveLanguageOptions' function ---
    const RetrieveLanguageOptions_Ref = useRef();
    RetrieveLanguageOptions_Ref.current = RetrieveLanguageOptions;
    // ---------------------------------------------------------------------------

    // retrieve the accent menu options
    function RetrieveAccentOptions() {

        if (selectedLanguage === '') {
            if (selectEnabled[4] === true) { SetSelectionEnables([true, true, true, true, false, false]) }
        }

        else {
            ResetSelections([resetSelect[0], resetSelect[1], resetSelect[2], resetSelect[3], !resetSelect[4], resetSelect[5]]);

            const engine = RetrieveSelectionKey(engineOptions, selectedEngine);
            const gender = RetrieveSelectionKey(genderOptions, selectedGender);
            const language = RetrieveSelectionKey(languageOptions, selectedLanguage);

            const foundAccents = [] // temp array

            for (var i = 0; i < avaliableVoices.length; i++) {

                if (avaliableVoices[i].SupportedEngines.includes(engine) === false || avaliableVoices[i].Gender !== gender || avaliableVoices[i].LanguageName.includes(language) === false) continue;
                else if (foundAccents.includes(avaliableVoices[i].LanguageName) === false) { foundAccents.push(avaliableVoices[i].LanguageName) }
            }

            GetAccentOptions(CreateMenuOptions(foundAccents));

            SetSelectionEnables([true, true, true, true, true, false]);
        }
    }
    // --------------------------------
    // --- a current value reference of the 'RetrieveAccentOptions' function ---
    const RetrieveAccentOptions_Ref = useRef();
    RetrieveAccentOptions_Ref.current = RetrieveAccentOptions;
    // -------------------------------------------------------------------------

    // retrieve the voice menu options
    function RetrieveVoiceOptions() {

        if (selectedAccent === '') {
            if (selectEnabled[5] === true) { SetSelectionEnables([true, true, true, true, true, false]) }
        }

        else {
            ResetSelections([resetSelect[0], resetSelect[1], resetSelect[2], resetSelect[3], resetSelect[4], !resetSelect[5]]);

            const engine = RetrieveSelectionKey(engineOptions, selectedEngine);
            const gender = RetrieveSelectionKey(genderOptions, selectedGender);
            const accent = RetrieveSelectionKey(accentOptions, selectedAccent);

            const foundVoices = [] // temp array

            for (var i = 0; i < avaliableVoices.length; i++) {

                if (avaliableVoices[i].SupportedEngines.includes(engine) === false || avaliableVoices[i].Gender !== gender || avaliableVoices[i].LanguageName.includes(accent) === false) continue;
                foundVoices.push(avaliableVoices[i].Id)
            }

            GetVoiceOptions(CreateMenuOptions(foundVoices));

            SetSelectionEnables([true, true, true, true, true, true]);
        }
    }
    // -------------------------------
    // --- a current value reference of the 'RetrieveVoiceOptions' function ---
    const RetrieveVoiceOptions_Ref = useRef();
    RetrieveVoiceOptions_Ref.current = RetrieveVoiceOptions;
    // ------------------------------------------------------------------------

    // -----------------------------------------------------------------------------------


    // submit the selected voice to the parent component via the 'SubmitVoiceSelectionEngine' and 'SubmitVoiceSelectionID' hook use state variables
    function SubmitSelectedVoice() {

        // if a voice has not been selected, submit an empty variable to the parent component via the 'SubmitVoiceSelectionID' hook use state variables
        if (selectedVoice === '' || selectedEngine === '') { SubmitVoiceSelectionID(''); }

        // submit the selected voice to the parent component via the 'SubmitVoiceSelectionID' hook use state function
        else { SubmitVoiceSelectionID(RetrieveSelectionKey(voiceOptions, selectedVoice)); } // retrieve the selected voices 'MenuItem's key name
    }
    // --------------------------------------------------------------------------------------------------------------------------------------------
    // --- a current value reference of the 'SubmitSelectedVoice' function ---
    const SubmitSelectedVoice_Ref = useRef();
    SubmitSelectedVoice_Ref.current = SubmitSelectedVoice;
    // -----------------------------------------------------------------------



    return (



        <Box id="boxWrapper-voicePicker" className="sectionWrapper">

            <div className="sectionHeading">

                {/* -------------------- module title -------------------- */}
                <h4>Voice Picker</h4>
                {/* ------------------------------------------------------ */}

            </div>

            <Stack id='selectionMenu-voicePicker' direction="column" spacing={1.5}>

                <SelectMenu defaultText={'Provider'} reset={resetSelect[0]} enabled={selectEnabled[0]} options={providerOptions} GetSelectionValue={GetSelectedProvider} />
                <SelectMenu defaultText={'Engine'} reset={resetSelect[1]} enabled={selectEnabled[1]} options={engineOptions} GetSelectionValue={GetSelectedEngine} />
                <SelectMenu defaultText={'Gender'} reset={resetSelect[2]} enabled={selectEnabled[2]} options={genderOptions} GetSelectionValue={GetSelectedGender} />
                <SelectMenu defaultText={'Language'} reset={resetSelect[3]} enabled={selectEnabled[3]} options={languageOptions} GetSelectionValue={GetSelectedLanguage} />
                <SelectMenu defaultText={'Accent'} reset={resetSelect[4]} enabled={selectEnabled[4]} options={accentOptions} GetSelectionValue={GetSelectedAccent} />
                <SelectMenu defaultText={'Voice'} reset={resetSelect[5]} enabled={selectEnabled[5]} options={voiceOptions} GetSelectionValue={GetSelectedVoice} />

            </Stack>

        </Box>



    )

}