import './MasterControlsModule.css';
import { useState, useEffect, useRef } from 'react';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Grid from '@mui/material/Grid';
import MenuItem from '@mui/material/MenuItem';
import SelectMenu from '../SelectMenu/SelectMenu';

export default function MasterControlsModule({ ssmlScriptWrapper, voiceSelectionEngine, UpdateSSMLScriptWrapper }) {

    // ---------------------------- RATE, PITCH, VOLUME SSML TAGS ----------------------------
    const SSMLTags = [];
    SSMLTags['rate'] = ['<prosody rate="20%"><7212590></prosody>', '<prosody rate="50%"><7212590></prosody>', '<prosody rate="100%"><7212590></prosody>', '<prosody rate="150%"><7212590></prosody>', '<prosody rate="200%"><7212590></prosody>'];
    SSMLTags['pitch'] = ['<prosody pitch="x-low"><7212590></prosody>', '<prosody pitch="low"><7212590></prosody>', '<prosody pitch="default"><7212590></prosody>', '<prosody pitch="high"><7212590></prosody>', '<prosody pitch="x-high"><7212590></prosody>'];
    SSMLTags['volume'] = ['<prosody volume="silent"><7212590></prosody>', '<prosody volume="x-soft"><7212590></prosody>', '<prosody volume="soft"><7212590></prosody>', '<prosody volume="default"><7212590></prosody>', '<prosody volume="loud"><7212590></prosody>', '<prosody volume="x-loud"><7212590></prosody>'];
    // ----------------------------------------------------------------------------------------

    // ---------------------------- DEFAULT TAG INDEXES FOR SSML TAGS ARRAY ----------------------------
    const defaultSettings = []; 
    defaultSettings['rate'] = 2; defaultSettings['pitch'] = 2; defaultSettings['volume'] = 3
    // -------------------------------------------------------------------------------------------------

    // ------------------------ SELECTED MENU OPTION INDEXES ------------------------
    const [selectedRateIndex, GetSelectedRateIndex] = useState(''); // currently selected rate option index
    useEffect(() => { ApplyTagToWrapper_Ref.current('rate', selectedRateIndex); }, [selectedRateIndex]) // triggers the current reference of the 'ApplyTagToWrapper' function every time 'selectedRateIndex' changes

    const [selectedPitchIndex, GetSelectedPitchIndex] = useState(''); // currently selected pitch option index
    useEffect(() => { ApplyTagToWrapper_Ref.current('pitch', selectedPitchIndex); }, [selectedPitchIndex]) // triggers the current reference of the 'ApplyTagToWrapper' function every time 'selectedPitchIndex' changes

    const [selectedVolumeIndex, GetSelectedVolumeIndex] = useState(''); // currently selected volume option index
    useEffect(() => { ApplyTagToWrapper_Ref.current('volume', selectedVolumeIndex); }, [selectedVolumeIndex]) // triggers the current reference of the 'ApplyTagToWrapper' function every time 'selectedVolumeIndex' changes
    // -------------------------------------------------------------------------------

    useEffect(() => { if (voiceSelectionEngine === 'neural') { RemoveTag_Ref.current('pitch'); } }, [voiceSelectionEngine]) // triggers the current reference of the 'RemoveTag' function every time 'voiceSelectionEngine' is changed to 'neural'
    


    // converts a specified menu options array into 'MenuItem' elements
    function CreateMenuOptions(options) {

        // if 'options' is undifined OR not of array type, log a message to the console and end this function immediately
        if (options === undefined || Array.isArray(options) === false) { console.log('no valid array parameter given'); return; }

        const menuItems = [] // temp array

        // cycle through each elements of the 'options' array
        for (var i = 0; i < options.length; i++) {

            const quotationMarks = [...options[i].matchAll('"')]; // gather every instance of the " character within the current 'options' element
            const tagValue = options[i].slice(quotationMarks[0].index + 1, quotationMarks[1].index); // isolate the word between each quotation mark from the current 'options' element

            menuItems[i] = <MenuItem key={tagValue} value={i}>{tagValue}</MenuItem>; // assemble 'tagValue' as an 'MenuItem' element
        }

        return menuItems; // return the assembled "Menu Item" key string array
    }
    // ---------------------------------------------------------------


     // ------ function is triggered every time an option is selected from any select menu (rate, pitch, volume) ------
    function ApplyTagToWrapper(taglabel, index) {

        // if 'taglabel' OR 'index' are undifined OR 'taglabel' is not of string type OR 'index' is of object type, log a message to the console and end this function immediately
        if (taglabel === undefined || typeof taglabel !== 'string' || index === undefined || typeof index === 'object') { console.log('no valid array parameter given'); return; }

        // --- if 'index' is set to an empty string (default selection)... ---
        if (index === '')
        {
            // if an SSML prosody opening and closing tag exist within 'ssmlScriptWrapper'...
            if (ssmlScriptWrapper.includes('<prosody ') && ssmlScriptWrapper.includes('</prosody>')) { index = defaultSettings[taglabel]; } // reset 'index' to the 'defaultSettings' index value corresponding with 'taglabel'
            else { return; } // if an SSML prosody opening and closing tag DOES NOT exist within 'ssmlScriptWrapper', end this function immediately
        }
        // -----------------------------------------------


        const focusTag = SSMLTags[taglabel][index].split('<7212590>')[0]; // isolate the opening tag of the tag in 'taglabel' at 'index' using the content point (<7212590>) as the divider (0 = opening tag, 1 = closing tag)

        if (ssmlScriptWrapper.includes(focusTag)) { return; } // if the opening tag of the focused tag exists within 'ssmlScriptWrapper', end this function immediately

        // --- if the opening tag of the focused tag DOES NOT exist within 'ssmlScriptWrapper'... ----
        else
        {
            // --- cycle through each of the ssml tags within the 'taglabel' group ---
            for (var i = 0; i < SSMLTags[taglabel].length; i++)
            {
                const compareTag = SSMLTags[taglabel][i].split('<7212590>')[0]; // isolate the opening tag of the current tag using the content point (<7212590>) as the divider (0 = opening tag, 1 = closing tag)
                // if the current compare tag exists within 'ssmlScriptWrapper', replace the current compare tag with the focused tag and end this function immediately
                if (ssmlScriptWrapper.includes(compareTag)) { UpdateSSMLScriptWrapper(ssmlScriptWrapper.replace(compareTag, focusTag)); return; }
            }
            // -----------------------------------------------------------------------

            // if no ssml tag within the 'taglabel' group is found within 'ssmlScriptWrapper', add the tag in 'taglabel' at 'index' to 'ssmlScriptWrapper' via replacing the content point (<7212590>) with the focused tag
            UpdateSSMLScriptWrapper(ssmlScriptWrapper.replace('<7212590>', SSMLTags[taglabel][index]));
        }
        // -------------------------------------------------------------------------------------------
    }
    // --- a current value reference of the 'ApplyTagToWrapper' function ---
    const ApplyTagToWrapper_Ref = useRef();
    ApplyTagToWrapper_Ref.current = ApplyTagToWrapper;
    // --------------------------------------------------------------------
    // -----------------------------------------------------------------------------------------------------------------


    // ------ function is triggered every time the value of 'voiceSelectionEngine' is changed to 'neural'  ------
    function RemoveTag(taglabel) {
        // --- cycle through each of the ssml tags within the 'taglabel' group ---
        for (var i = 0; i < SSMLTags[taglabel].length; i++) {

            const compareTag = SSMLTags[taglabel][i].split('<7212590>')[0]; // isolate the opening tag of the current tag using the content point (<7212590>) as the divider (0 = opening tag, 1 = closing tag)

            // if the current compare tag exists within 'ssmlScriptWrapper', remove the compare tag & its cooresponding closing tag from 'ssmlScriptWrapper' and end this function immediately
            if (ssmlScriptWrapper.includes(compareTag)) { UpdateSSMLScriptWrapper(ssmlScriptWrapper.replace(compareTag, '').replace('</prosody>', '')); return; }
        }
        // -----------------------------------------------------------------------
    }
    // --- a current value reference of the 'RemoveTag' function ---
    const RemoveTag_Ref = useRef();
    RemoveTag_Ref.current = RemoveTag;
    // --------------------------------------------------------------------
    // -----------------------------------------------------------------------------------------------------------



    return (



        <Box id="boxWrapper-masterControls" className="sectionWrapper">

            <div className="sectionHeading">

                {/* -------------------- module title -------------------- */}
                <h4>Master Controls</h4>
                {/* ------------------------------------------------------ */}

            </div>


            <Stack id='controlsMenu-masterControls' direction="column">

                {/* --------------------------------- rate control --------------------------------- */}
  
                <Grid container id='rateControlSection-masterControls' className='controlSection-masterControls'>

                    <Grid item xs={6}>
                        <h4>Rate</h4>
                    </Grid>

                    <Grid item xs={6}>
                        <SelectMenu defaultText={'unset'} enabled={true} options={CreateMenuOptions(SSMLTags.rate)} GetSelectionValue={GetSelectedRateIndex} />
                    </Grid>

                </Grid>
          
                {/* -------------------------------------------------------------------------------- */}


                {/* -------------------------------- pitch control --------------------------------- */}
                <Grid container id='pitchControlSection-masterControls' className='controlSection-masterControls'>

                    <Grid item xs={6}>
                        <h4>Pitch</h4>
                    </Grid>

                    <Grid item xs={6}>
                        {/* 'reset' is triggered and 'enabled' is set to false if the selected voice engine is 'neural'*/}
                        <SelectMenu defaultText={'unset'} reset={voiceSelectionEngine === 'neural'} enabled={voiceSelectionEngine !== 'neural'} options={CreateMenuOptions(SSMLTags.pitch)} GetSelectionValue={GetSelectedPitchIndex} />
                    </Grid>

                </Grid>
   
                {/* -------------------------------------------------------------------------------- */}


                {/* -------------------------------- volume control -------------------------------- */}
               
                <Grid container id='volumeControlSection-masterControls' className='controlSection-masterControls'>

                    <Grid item xs={6}>
                        <h4>Volume</h4>
                    </Grid>

                    <Grid item xs={6}>
                        <SelectMenu defaultText={'unset'} enabled={true} options={CreateMenuOptions(SSMLTags.volume)} GetSelectionValue={GetSelectedVolumeIndex} />
                    </Grid>

                </Grid>
              
                {/* -------------------------------------------------------------------------------- */}
   
            </Stack>

        </Box>



    )
}