import './TopMenuModule.css';
import Stack from '@mui/material/Stack';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import $ from 'jquery'

export default function TopMenuModule() {

    function SubmitForm() {

        //$("#msgResponse").html("<div class='alert alert-warning mb-0' role='alert'><i class='fas fa-sync fa-spin'></i> Updating...</div>");

        $('#speakEasyAjaxForm').submit(function (event) {
            event.preventDefault();
            var form = $(this);
            $.ajax({
                type: form.attr('method'),
                url: form.attr('action'),
                data: form.serialize()

            }).done(function (data) {
                //$("#msgResponse").html("<div class='alert alert-success mb-0' role='alert'><i class='fas fa-check-circle'></i> success.</div>");

            }).fail(function (data) {
                //$("#msgResponse").html("<div class='alert alert-success mb-0' role='alert'><i class='fas fa-check-circle'></i> failed.</div>");
            });
        });
        $('#speakEasyAjaxForm').submit();
    }

    return (

        <Grid container id='gridContainer-top'>



            <Grid item xs={6}> {/* ---------------------------- LEFT MENU ---------------------------- */}
                               
                <Stack id='leftMenu-top' direction="row" spacing={1}>

                    {/* -------------------- open button -------------------- */}
                    <div><Button
                        className="button mediumButton"
                        variant="contained"

                        onClick={null}
                    > Open </Button></div>
                    {/* ----------------------------------------------------- */}

                    {/* -------------------- save button -------------------- */}     
                    <div><Button
                        id="mySubmitButton"
                        className="button mediumButton"
                        variant="contained"

                        onClick={() => { SubmitForm(); }}
                    > Save </Button></div>
                    {/* ----------------------------------------------------- */}

                    {/* -------------------- print button -------------------- */}
                    <div><Button
                        className="button mediumButton"
                        variant="contained"

                        onClick={null}
                    > Print </Button></div>
                    {/* ------------------------------------------------------ */}

                </Stack>
            </Grid> {/* ---------------------------- LEFT MENU ---------------------------- */}


            <Grid item xs={6}> {/* ---------------------------- RIGHT MENU ---------------------------- */}
                <Stack id='rightMenu-top' direction="row" spacing={1}>

                    {/* -------------------- submit button -------------------- */}
                    <div><Button
                        className="button mediumButton"
                        variant="contained"

                        onClick={null}
                    > Submit </Button></div>
                    {/* ------------------------------------------------------- */}

                    {/* -------------------- options button -------------------- */}
                    <div><Button
                        className="button mediumButton"
                        variant="contained"

                        onClick={null}
                    > Options </Button></div>
                    {/* -------------------------------------------------------- */}

                    {/* -------------------- user button -------------------- */}
                    <div><Button
                        className="button mediumButton"
                        variant="contained"

                        onClick={null}
                    > User </Button></div>
                    {/* ----------------------------------------------------- */}

                </Stack>
            </Grid> {/* ---------------------------- RIGHT MENU ---------------------------- */}



            <form id="speakEasyAjaxForm" action="https://speakeasypro.ca/public/api/module-items/update" method="post" encType="multipart/form-data">

                <input type="hidden" name="ItemId" value="2068" />
                <input type="hidden" name="ModuleId" value="2065" required />
                <input type="hidden" name="redirectURL" value=""/>
                <input type="hidden" name="prop_Title" value="this will display" />

            </form>

        </Grid>

    )
}