import './DownloadAudioBtn.css';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

export default function DownloadAudioBtn({ submittedScript, RequestSpeechFile}) {

    return (



        <Box id="boxWrapper-download">



            {/* -------------------- download audio button -------------------- */}
            <Button
                id="downloadAudioButton"
                className="button"
                variant="contained"


                onClick={() => RequestSpeechFile(submittedScript)}
            > Download Audio </Button>
            {/* --------------------------------------------------------------- */}



        </Box>



    )
}