import './App.css';
import { useState, useEffect, useRef } from 'react';
import TopMenuModule from './components/TopMenuModule/TopMenuModule';
import MasterControlsModule from './components/MasterControlsModule/MasterControlsModule';
import ScriptInputModule from './components/ScriptInputModule/ScriptInputModule';
import VoicePickerModule from './components/VoicePickerModule/VoicePickerModule';
import EditByWordModule from './components/EditByWordModule/EditByWordModule';
import DownloadAudioBtn from './components/DownloadAudioBtn/DownloadAudioBtn';
import Title from './components/Title/Title';
import EditScriptModule from './components/EditScriptModule/EditScriptModule';
import Stack from '@mui/material/Stack';



export default function App() {

    const AWS = require('aws-sdk'); // initiate the Amazon Web Services SDK
    AWS.config.region = 'ca-central-1'; // configure the Amazon Web Services region to the nearest region
    // configure the Amazon Web Services credentials to a random access key ID & a random secret access key generated by the AWS Cognito service
    AWS.config.credentials = new AWS.CognitoIdentityCredentials({ IdentityPoolId: 'ca-central-1:bf4fdb3d-b7e1-4453-a8f4-647aa689377f', });

    // set up an Amazon Polly profile using the Amazon Web Services configuration and credentials set in 'AWS'
    const AWS_polly = new AWS.Polly({
        region: AWS.config.region,
        accessKeyId: AWS.config.accessKeyId,
        secretAccessKey: AWS.config.secretAccessKey,
    });

    const audioPlayer = new Audio(); // controls all returned text to speech audio

    const [ssmlScriptWrapper, ReceiveSSMLScriptWrapper] = useState('<speak><7212590></speak>'); // recieves, holds, and sends the ssml wrapper for the submitted script to and from the 'MasterControlsModule' component

    const [receivedScript, ReceiveScript] = useState(''); // recieves, holds, and sends the sssml tag formatted master script string to and from child components

    const [receivedCurrentWordsGroups, ReceiveCurrentSelectedWordsGroups] = useState([ [], [] ]); // recieves, holds, and sends the selected words groups array to and from child components
    const [receivedNewWords, ReceiveNewSelectedWords] = useState(''); // recieves, holds, and sends the ssml tag formated selected string to and from child components
    
    const [voiceSelectionEngine, ReceiveVoiceSelectionEngine] = useState(''); // recieves and holds, and sends the voice selected ID to and from child components
    const [voiceSelectionID, ReceiveVoiceSelectionID] = useState(''); // recieves and holds, and sends the voice selected engine to and from child components
    const [scriptPlaybackLength, UpdatePlaybackLength] = useState('00:00'); // recieves and holds, and sends the formatted duration of the script audio playback

    const [clearSSML, TriggerSSMLClear] = useState(false); // signals all SSML tags to clear from the script when updated, sends this trigger to and from child components

    const [modifyScriptMode, UpdateModifyScriptStatus] = useState(false); // recieves and holds, and sends the current active status of modify script mode to and from child components
    const [receivedEditingText, ReceiveEditingText] = useState(null); // recieves and holds, and sends the editing script string text selection to and from child components
    const [receivedEditedText, ReceiveEditedText] = useState(null); // recieves, holds, and sends the sssml tag formatted master script string to and from child components

    var activePreviewButton; // keep track of the most recent 'preview button' clicked


    // ------------------------------------------------------------ Triggers every time the value of 'modifyScriptMode' is changed ------------------------------------------------------------
    useEffect(() => {

        switch (modifyScriptMode) {

            // ------------------------- MODIFY SCRIPT MODE ENABLED -------------------------
            case true:

                document.getElementById('boxWrapper-selectionView').style.display = "none"; // hide the "Selection View"
                document.getElementById('boxWrapper-modifyScriptView').style.display = "block"; // display the "Modify Script View"
                break;
            // ------------------------------------------------------------------------------

            // ------------------------ MODIFY SCRIPT MODE DISABLED -------------------------
            case false:

                document.getElementById('boxWrapper-modifyScriptView').style.display = "none"; // hide the "Modify Script View"
                document.getElementById('boxWrapper-selectionView').style.display = "block"; // display the "Selection View"
                break;
            // ------------------------------------------------------------------------------

            default:
                break;
        }

    }, [modifyScriptMode])
    // --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------

    // ------------------------------------------------------------ Triggers every time the value of 'voiceSelectionID' is changed ------------------------------------------------------------
    useEffect(() => { RequestSSMLClear_Ref.current(); }, [voiceSelectionID]) // execute the current reference of the 'RequestSSMLClear' function
    // ----------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------



    // ------------- function is triggered when the audio player naturally stops playing -------------
    audioPlayer.onended = function () {

        // change the 'activePreviewButton' label to 'Preview' and clear the 'activePreviewButton'
        //activePreviewButton.innerText = 'Preview';
        activePreviewButton = null;

    };
    // -----------------------------------------------------------------------------------------------

    // ---------------------------------------------------- function is triggered when the audio player duration changes ----------------------------------------------------
    audioPlayer.ondurationchange = function () {
        UpdatePlaybackLength(FormatDuration(audioPlayer.duration)) // update scriptPlaybackLength to the formatted version of 'audioPlayer's current duration via 'UpdatePlaybackLength' (use state function)
    }
    // ----------------------------------------------------------------------------------------------------------------------------------------------------------------------

    // --- function recieves a number value and is expected to return the number as string value in a 'mm:ss' format ---
    function FormatDuration(secondsDuration) {

        // if 'secondsDuration' is undifined OR not of number type, log a message to the console and end this function immediately
        if (secondsDuration === undefined || typeof secondsDuration !== 'number') { console.log('no valid string parameter given'); return; }

        var seconds = Math.floor(secondsDuration); // convert 'secondsDuration' into a whole number (rounds to nearest integer), save as 'seconds'
        var minutes = Math.floor(seconds / 60); // get the minute count of 'seconds' by dividing it by 60, save as 'minutes'
        minutes = minutes >= 10 ? minutes : '0' + minutes; // if 'minutes' is less than 10, add a '0' before 'minutes'
        seconds = Math.floor(seconds % 60); // limits 'seconds' to 60
        seconds = seconds >= 10 ? seconds : '0' + seconds; // if 'seconds' is less than 10, add a '0' before 'seconds'

        return minutes + ':' + seconds; // return the recieved number as a formatted string
    }
    // -----------------------------------------------------------------------------------------------------------------
    // ----------------------------------------------------------------------------------------------------------------------------------------------------------------------


    // ---------------------- function is triggered every time a 'preview button' is clicked ----------------------
    function PlaybackSpeech(stringSpeech, button) {

        audioPlayer.pause(); // stop the audio player if it is currently playing
        
        // if no 'stringSpeech' has been given, or no 'voiceSelectionID' or 'voiceSelectionEngine' is currently set, or 'button' has not been given, end this function at this point
        if (stringSpeech === null || (stringSpeech.trim()).length === 0 || voiceSelectionID === "" || voiceSelectionEngine === "" || button === null) { return; }

        // --- variables Amazon Polly requires to generate speech from text ---
        const input = {
            Text: ssmlScriptWrapper.replace('<7212590>', stringSpeech),//replace <7212590> with the ssml script in the ssml wrapper, // the text to convert to speech
            TextType: "ssml", // the way Amazon reads the text
            OutputFormat: "mp3", // the output file type
            VoiceId: voiceSelectionID, // the Amazon playback voice
            Engine: voiceSelectionEngine, // the Amazon speech engine used to translate text to speech
        }
        // ---------------------------------------------------------------------

        // if the 'activePreviewButton' is the 'button' element,  change the 'activePreviewButton' label to 'Preview', clear the 'activePreviewButton', and end this function at this point
        if (activePreviewButton === button) { /*button.innerText = 'Preview';*/ activePreviewButton = null; return; }

        activePreviewButton = button; // set the 'activePreviewButton' to the 'button' element


        AWS_polly.synthesizeSpeech(input, (err, data) => { // send a generate speech from text request to Amazon Polly

            // if the request to Amazon Polly could/was not receieved or speech data could/was not returned, log the specific encountered error to the console and end this function at this point
            if (err) { console.log(err); return; }

            // --- convert the returned speech data into an audio format the audio player can read and play ---
            const uInt8Array = new Uint8Array(data.AudioStream);
            const arrayBuffer = uInt8Array.buffer;
            const blob = new Blob([arrayBuffer], { type: 'audio/mpeg' });
            const url = URL.createObjectURL(blob);
            // -------------------------------------------------------------------------------------------------

            // set the audio player's source to the readable speech data and play the audio player
            audioPlayer.src = url;
            audioPlayer.play();

            /*for (var i = 0; i < document.getElementsByClassName('previewButton').length; i++) { // cycles through every element with the 'previewButton' class attached

                if (document.getElementsByClassName('previewButton')[i] === button) { button.innerText = 'Stop'; } // if the found element is the 'button' element, change 'button' label to 'Stop'

                else { document.getElementsByClassName('previewButton')[i].innerText = 'Preview'; } // or else, change the found elements label to 'Preview'

            }*/
        })
    }
    // ------------------------------------------------------------------------------------------------------------

    // ------------------------------ function is triggered when the 'Download Audio' button is clicked ------------------------------
    function GenerateSpeechFile(stringSpeech) {
      
        // if no 'stringSpeech' has been given, or no 'voiceSelectionID' or 'voiceSelectionEngine' is currently set, end this function at this point
        if (stringSpeech === null || (stringSpeech.trim()).length === 0 || voiceSelectionID === "" || voiceSelectionEngine === "") { return; }
        
        // --- variables Amazon Polly requires to generate speech from text ---
        const input = {
            Text: "<speak>" + stringSpeech + "</speak>", // the text to convert to speech
            TextType: "ssml", // the way Amazon reads the text
            OutputFormat: "mp3", // the output file type
            VoiceId: voiceSelectionID, // the Amazon playback voice
            Engine: voiceSelectionEngine, // the Amazon speech engine used to translate text to speech
        }
        // ---------------------------------------------------------------------


        AWS_polly.synthesizeSpeech(input, (err, data) => { // send a generate speech from text request to Amazon Polly

            // if the request to Amazon Polly could/was not receieved or speech data could/was not returned, log the specific encountered error to the console and end this function at this point
            if (err) { console.log(err); return; }

            // --- convert the returned speech data into an audio format the audio player can read and play ---
            const uInt8Array = new Uint8Array(data.AudioStream);
            const arrayBuffer = uInt8Array.buffer;
            const blob = new Blob([arrayBuffer], { type: 'audio/mpeg' });
            const url = URL.createObjectURL(blob);
            // -------------------------------------------------------------------------------------------------


            // --- generates and downloads the requested audio file, the audio file is saved to the default location specificed by the browser ---
            const DownloadAudioFile = async (url) => {

                const downloader = document.createElement('a'); // create an HTML link element

                downloader.download = 'speech-playback.mp3'; // name of the file to be generated
                downloader.href = url; // link to the readable speech data

                // a click event for the HTML link element that triggers when the 'DownloadAudioFile' is executed
                downloader.addEventListener('click', (e) => { setTimeout(() => URL.revokeObjectURL(downloader.href), 30 * 1000); }); // this event sends the generate and download file request to Amazon Polly
                downloader.click();

                downloader.remove(); // remove the HTML link element
            };
            DownloadAudioFile(url); // trigger the 'DownloadAudioFile' function using the readable speech data as a parameter
            // --------------------------------------------------------------------------------------------------------------------------------------

        })
    }
    // -------------------------------------------------------------------------------------------------------------------------------

    // ------------------------------ function is triggered every time  'voiceSelectionID' is updated ------------------------------
    function RequestSSMLClear() {
        TriggerSSMLClear(!clearSSML); // updates 'clearSSML' via current reference to useState hook function 'TriggerSSMLClear'
    }
    // --- a current value reference of the 'RequestSSMLClear' function ---
    const RequestSSMLClear_Ref = useRef();
    RequestSSMLClear_Ref.current = RequestSSMLClear;
    // ------------------------------------------------------------------
    // ------------------------------------------------------------------------------------------------------------------------------



    return (

        <>

            <Stack id='topUI-app'direction="row">

                <MasterControlsModule id='topUI-left-app' ssmlScriptWrapper={ssmlScriptWrapper} voiceSelectionEngine={voiceSelectionEngine} UpdateSSMLScriptWrapper={ReceiveSSMLScriptWrapper}
                    /* renders  the 'MasterControlsModule' compomnent,
                     * share 'ssmlScriptWrapper' to the 'MasterControlsModule' component,
                     * share 'voiceSelectionEngine' to the 'MasterControlsModule' component,
                     * recieve 'ReceiveSSMLScriptWrapper' UseState hook calls from the 'MasterControlsModule' compomnent via its 'UpdateSSMLScriptWrapper' */
                />

                <div id='topUI-center-app'>

                    <TopMenuModule /* renders  the 'TopMenuModule' compomnent */ />

                    <ScriptInputModule updatedSelectedWords={receivedNewWords} returnedEditedText={receivedEditedText} editingScript={modifyScriptMode} scriptPlaybackLength={scriptPlaybackLength} triggerClearSSML={clearSSML}
                        SubmitSelectedWordsGroups={ReceiveCurrentSelectedWordsGroups} SubmitScript={ReceiveScript} PlaybackScript={PlaybackSpeech} SubmitEditingText={ReceiveEditingText} ResetEditedText={ReceiveEditedText} UpdateEditingScript={UpdateModifyScriptStatus}
                    /* renders  the 'ScriptInputModule' compomnent,
                     * sends 'receivedNewWords' to the 'ScriptInputModule' component (stored within 'updatedSelectedWords'),
                     * sends 'receivedEditedText' to the 'ScriptInputModule' component (stored within 'returnedEditedText'),
                     * sends 'modifyScriptMode' to the 'ScriptInputModule' component (stored within 'editingScript'),
                     * sends 'scriptPlaybackLength' to the 'ScriptInputModule' component (stored within 'scriptPlaybackLength'),
                     * sends 'clearSSML' to the 'ScriptInputModule' component (stored within 'triggerClearSSML'),
                     * recieves 'SubmitSelectedWordsGroups' from the 'ScriptInputModule' compomnent (triggers 'ReceiveCurrentSelectedWordsGroups' UseState hook function),
                     * recieves 'SubmitScrip' from the 'ScriptInputModule' compomnent (triggers 'ReceiveScript' UseState hook function),
                     * recieves the 'PlaybackSpeech' function from the 'ScriptInputModule' component (triggers 'PlaybackSpeech' function),
                     * recieves 'SubmitEditingText' from the 'ScriptInputModule' compomnent (triggers 'ReceiveEditingText' UseState hook function),
                     * recieves 'ResetEditedText' from the 'ScriptInputModule' compomnent (triggers 'ReceiveEditedText' UseState hook function),
                     * recieves 'UpdateEditingScript' from the 'ScriptInputModule' compomnent (triggers 'UpdateModifyScriptStatus' UseState hook function)*/ />

                </div>

                <VoicePickerModule id='topUI-right-app' AWS_polly={AWS_polly} SubmitVoiceSelectionEngine={ReceiveVoiceSelectionEngine} SubmitVoiceSelectionID={ReceiveVoiceSelectionID}
                /* renders the 'VoicePickerModule' compomnent,
                 * sends 'AWS_polly' to the 'VoicePickerModule' component (stored within 'AWS_polly'),
                 * recieves 'SubmitVoiceSelectionEngine' from the 'VoicePickerModule' component (triggers 'ReceiveVoiceSelectionEngine' UseState hook function)
                 * recieves 'SubmitVoiceSelectionID' from the 'VoicePickerModule' component (triggers 'ReceiveVoiceSelectionID' UseState hook function) */ />

            </Stack>

            <EditScriptModule selectedEditingText={receivedEditingText} SubmitEditingText={ReceiveEditingText} SubmitEditedText={ReceiveEditedText} UpdateEditingScript={UpdateModifyScriptStatus}
            /* renders  the 'EditScriptModule' compomnent,
            * sends 'receivedEnableEditing' to the 'EditScriptModule' component (stored within 'selectedEditingText'),
            * recieves 'SubmitEditingText' from the 'EditScriptModule' compomnent (triggers 'ReceiveEditingText' UseState hook function),
            * recieves 'SubmitEditedText' from the 'EditScriptModule' compomnent (triggers 'ReceiveEditedText' UseState hook function)
            * recieves 'UpdateEditingScript' from the 'EditScriptModule' compomnent (triggers 'UpdateModifyScriptStatus' UseState hook function)*/ />

            <EditByWordModule selectedWordsGroups={receivedCurrentWordsGroups} selectedVoiceEngine={voiceSelectionEngine} ssmlScriptWrapper={ssmlScriptWrapper} SubmitUpdatedWords={ReceiveNewSelectedWords} PlaybackSelection={PlaybackSpeech}
            /* renders  the 'EditByWordModule' compomnent,
             * sends 'receivedCurrentWordsGroups' to the 'EditByWordModule' component (stored within 'selectedWordsGroups'),
             * sends 'voiceSelectionEngine' to the 'EditByWordModule' component (stored within 'selectedVoiceEngine'),
             * share 'ssmlScriptWrapper' to the 'MasterControlsModule' component,
             * recieves 'SubmitUpdatedWords' from the 'EditByWordModule' compomnent (triggers 'ReceiveNewSelectedWords' UseState hook function),
             * recieves the 'PlaybackSpeech' function from the 'EditByWordModule' component (triggers 'PlaybackSpeech' function) */ />

            <DownloadAudioBtn submittedScript={receivedScript} RequestSpeechFile={GenerateSpeechFile}
            /* renders  the 'DownloadAudioBtn' compomnent,
             * sends 'receivedScript' to the 'DownloadAudioBtn' component (stored within 'submittedScript'),
             * recieves the 'RequestSpeechFile' function from the 'DownloadAudioBtn' component (triggers 'GenerateSpeechFile' function) */ />

            <Title /* renders  the 'Title' compomnent */ />

        </>
    )

}
