import './Title.css';

const title = "Speak Easy" // text that will display as the application title
const subtitle = "Twisted Pair Productions & Creative Saskatchewan" // text that will display as the application subtitle



export default function Title() {

    return (



        <>

            {/* -------------------- application title -------------------- */}
            <h1 id="title">{title}</h1>
            {/* ----------------------------------------------------------- */}

            {/* -------------------- application subtitle -------------------- */}
            <h3 id="subtitle">{subtitle}</h3>
            {/* -------------------------------------------------------------- */}

        </>



    )
}