import React, { useState, useEffect, useRef } from 'react'
import './ScriptInputModule.css';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';



export default function ScriptInputModule({ updatedSelectedWords, returnedEditedText, editingScript, scriptPlaybackLength, triggerClearSSML, SubmitSelectedWordsGroups, SubmitScript, PlaybackScript, SubmitEditingText, ResetEditedText, UpdateEditingScript }) {

    const [wordCount, UpdateWordCount] = useState(0); // 'wordCount' variable stores the current word count of the master script. 'UpdateWordCount' updates the 'wordCount' variable

    const [stringScript, SetStringScript] = useState(''); // 'scriptScript' variable stores the raw string version of the master scrtipt. 'SetSSMLScript' updates the 'scriptScript' variable
    const [ssmlScript, SetSSMLScript] = useState(''); // 'ssmlScript' variable stores the ssml tagged version of the master scrtipt. 'SetSSMLScript' updates the 'ssmlScript' variable
    const [selectedStartEndIndex, SetSelectedStartEndIndex] = useState([-1, -1]) // stores the current START word (element 0) and END word (element 1) of the selected text relating to the script word arrays
    const [enableEditing, SetEnableEditing] = useState(!editingScript); // 'enableEditing' indicates if editing of select text from the master script is enabled. 'SetEnableEditing' updates the 'enableEditing' variable

    // ------------------------------------------------------------ Triggers every time the value of 'editingScript' is changed ------------------------------------------------------------
    useEffect(() => {

        if (editingScript === true) { SetEnableEditing(false) } // if 'editingScript' has been updated to true, set 'enableEditing' to false via useState hook function 'SetEnableEditing'
        else if (NoScriptFound_Ref.current() === true) { SetEnableEditing(true); }
        /* if the current 'NoScriptFound' returns true, set 'enableEditing' to true via useState hook function 'SetEnableEditing' */

    }, [editingScript])

    // ------ Triggers every time the value of 'editingScript' is changed - THIS FUNCTION IS REQUIRED TO AVOID WARNING LOGS WITH THE 'useEffect' HOOK ------
    function NoScriptFound() {

        // if 'editingScript' has been updated to false AND no edited text is being returned AND there is no text currently in the script text field...
        if (returnedEditedText === null && stringScript === '') { return true; } // return a 'true' value
        else { return false; } // or else, return a 'false' value
    }
    // --- a current value reference of the 'NoScriptFound' function ---
    const NoScriptFound_Ref = useRef();
    NoScriptFound_Ref.current = NoScriptFound;
    // ------------------------------------------------------------------
    // ------------------------------------------------------------------------------------------------------------------------------------------------------
    // --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
    
    // ------------------------------------------------------------ Triggers every time the value of 'updatedSelectedWords' is changed ------------------------------------------------------------
    useEffect(() => { AssembleSSMLScript_Ref.current(); }, [updatedSelectedWords])
    // executes the current reference to the 'AssembleSSMLScript' function when 'updatedSelectedWords' is updated
    // --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------

    // ------------------------------------------------------------ Triggers every time the value of 'returnedEditedText' is changed ------------------------------------------------------------
    useEffect(() => { if (returnedEditedText !== null) { ReplaceOutdatedScript_Ref.current(); } }, [returnedEditedText])
    // executes the current reference to the 'ReplaceOutdatedScript' function when 'returnedEditedText' is updated
    // --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------

    // ------------------------------------------------------------ Triggers every time the value of 'clearSSMLScript' is changed ------------------------------------------------------------
    useEffect(() => { ClearSSMLScript_Ref.current(); }, [triggerClearSSML])
    // executes the current reference to the 'ClearSSMLScript' function when 'triggerClearSSML' is updated
    // ---------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------


    // ----- function is triggered every time a mouse button is pressed while the mouse cursor is overlapping the text field -----
    function ClearSelection() {

        // check to see if any text is selected. If text is selected, deselect it
        if (window.getSelection) { window.getSelection().removeAllRanges(); }
        else if (document.selection) { document.selection.empty(); }
        
        SubmitSelectedWordsGroups([[], []]); // clear the selected words groups via 'SubmitSelectedWordsGroups' global reference
    }
    // ---------------------------------------------------------------------------------------------------------------------------


    // ----- function recieves a string and is expected to return an array of each individual word of the string -----
    function SplitString(textString) {

        // If the given parameter variable is not a set string, send a message to the console and end this function immediately
        if (textString === false) { console.log("parameter not set"); return; }

        const editedTagsString = textString.replaceAll('<prosody ', '<prosody*').replaceAll('<emphasis ', '<emphasis*').replaceAll('<break ', '<break*');  // find all instances of ssml tags within the 'textString' and replace any ' ' within those ssml tags with '*' characters
        // this is necessary to avoid splitting the 'textString' at the wrong place below

        // replace any line breaks in 'teditedTagsString' with a " " (replace), split 'editedTagsString' where a " " is found (split) into array elements, then remove any "" elements from 'textArray' (trim)
        var textArray = editedTagsString.replace(/\r?\n|\r/g, ' ').split(' ').filter(entry => entry.trim() !== '');

        // cycle through each element of 'textArray', find all instances of ssml tags within the elements and replace any '*' within those ssml tags with ' ' characters
        // now that 'textString' is divided into 'textArray' the ssml tags can be restored to their correct syntax
        for (var i = 0; i < textArray.length; i++){
            if (textArray[i].includes('<prosody*')) { textArray[i] = textArray[i].replaceAll('<prosody*', '<prosody '); }
            if (textArray[i].includes('<emphasis*')) { textArray[i] = textArray[i].replaceAll('<emphasis*', '<emphasis '); }
            if (textArray[i].includes('<break*')) { textArray[i] = textArray[i].replaceAll('<break*', '<break '); }
        }
        // -------------------------------------------------------------------------------------------------------------------------------------------------------------

        return (textArray); // return the recieved string as an array of words
    }
    // ----------------------------------------------------------------------------------------------------------------


    // ----- function recieves an array and is expected to return a single string of each array element combined, seperated by " " -----
    function ConvertArrayToString(textArray) {

        // --- cycles through all the words (elements) within 'textArray' and convert them back into a single string ('textString'). each element is seperated by a " " character within 'textString' ---
        var textString = '';
        for (var n = 0; n < textArray.length; n++) {
            textString = textString += textArray[n]
            if (n !== textArray.length - 1) { textString = textString += " "; }
        }
        // ----------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------

        return textString; // return the recieved array as a single script
    }
    // ---------------------------------------------------------------------------------------------------------------------------------


    // ------------------------------ function is triggered when the 'Edit Script' button is clicked ------------------------------
    function SubmitEditSelection() {

        const submitSelectedWordsArray = SplitString(stringScript).slice(selectedStartEndIndex[0], selectedStartEndIndex[1] + 1); // convert ONLY THE CURRENT SELECTED WORDS of 'stringScript' (string) into 'stringScriptArray' (submitSelectedWordsArray)
        const ssmlSelectedWordsArray = SplitString(ssmlScript).slice(selectedStartEndIndex[0], selectedStartEndIndex[1] + 1); // convert ONLY THE CURRENT SELECTED WORDS of 'ssmlScript' (string) into 'ssmlScriptArray' (ssmlSelectedWordsArray)

        // --- cycles through all words in 'submitSelectedWordsArray' ---
        for (var i = 0; i < submitSelectedWordsArray.length; i++) {
            // If the current word contains a '_' AND the word with the cooresponding index in 'ssmlSelectedWordsArray' contains a '<break>' tag, replace the current word with the word with the cooresponding index in 'ssmlSelectedWordsArray'
            if (submitSelectedWordsArray[i].includes('_') && ssmlSelectedWordsArray[i].includes('<break time="')) { submitSelectedWordsArray[i] = ssmlSelectedWordsArray[i] }
        }
        // --------------------------------------------------------------

        const submitString = ConvertArrayToString(submitSelectedWordsArray); // convert 'submitSelectedWordsArray' (array) into 'submitString' (string)

        SubmitEditingText(submitString); // Submit 'submitString' to the parent component via the 'SubmitEditingText' react hook function

        UpdateEditingScript(true); // enable master script text editing mode

        ClearSelection(); // clear the selected text in the master script Text Field element
    }
    // ----------------------------------------------------------------------------------------------------------------------------


    // ----- function is triggered every time a mouse button is released while the mouse cursor is overlapping the text field -----
    function GetSelectionWords(masterScript) {

        if (masterScript === undefined || typeof masterScript !== 'object') return; // if 'masterScript' is undifined OR not of object type, end this function immediately

        const stringScriptArray = SplitString(stringScript); // convert 'stringScript' (string) into 'stringScriptArray' (array)
        const ssmlScriptArray = SplitString(ssmlScript); // convert 'ssmlScript' (string) into 'ssmlScriptArray' (array)

        const masterScriptString = masterScript.textContent; // get the inner string text of 'masterScript' and save it as 'masterScriptString'
        const selectedScriptString = window.getSelection().toString(); // get the current selected string text and save it as 'selectedScriptString'

        if (masterScriptString === '') { SetEnableEditing(true); } // if no characters exist within 'masterScriptString', set 'enableEditing' to true via the 'SetEnableEditing' react useState hook function
        else { // if characters exist within 'masterScriptString'...
            // check if any characters exists within 'selectedScriptString', set 'enableEditing' according to if characters are found via the 'SetEnableEditing' react useState hook function
            SetEnableEditing(selectedScriptString.length !== 0);
        }

        // if 'selectedScriptString' does not contain any characters, update the string script and the master script display to 'stringScriptArray' as a single string and end this function immediately
        if (selectedScriptString.length === 0) { UpdateStringScript(stringScriptArray, ssmlScriptArray); return; }
        
        var selectionStart = 0; var selectionEnd = 0; // define the start ('selectionStart') and end ('selectionEnd') character indexes of the current selected string text
        // if the current selected string text's 'anchorOffset' is less or equal to the current selected string text's 'focusOffset', set 'selectionStart' as 'anchorOffset' & 'selectionEnd' as 'focusOffset'
        if (window.getSelection().anchorOffset <= window.getSelection().focusOffset) { selectionStart = window.getSelection().anchorOffset; selectionEnd = window.getSelection().focusOffset; }
        
        // if the current selected string text's 'anchorOffset' is greater than the current selected string text's 'focusOffset', set 'selectionStart' as 'focusOffset' & 'selectionEnd' as 'anchorOffset'
        else { selectionStart = window.getSelection().focusOffset; selectionEnd = window.getSelection().anchorOffset; }
        
        // --- find the character index that represents the START of the FIRST selected word within 'textField' and save the located index as 'startSelectionIndex ' ---
        var startSelectionIndex = 0; 
        for (var s = selectionStart; s >= 0; s--) { // cycles through every character before the 'selectionStart' index within 'textField'
            if (masterScriptString[s] === ' ') { startSelectionIndex = s + 1; break; } // if the current character is an empty space, set the index of that character + 1 as 'startSelectionIndex' and break out of this loop immediately
            else if (s === 0) { startSelectionIndex = s; } // if the current character the first character, set the index of that character as 'startSelectionIndex'
        }
        // ----------------------------------------------------------------------------------------------------------------------------------------------------

        // --- find the character index that represents the END of the LAST selected word within 'textField' and save the located index as 'endSelectionIndex' ---
        var endSelectionIndex = 0;
        for (var e = selectionEnd; e <= masterScriptString.length; e++) { // cycles through every character after the 'selectionEnd' index within 'textField'
            if (e === masterScriptString.length || masterScriptString[e] === ' ') { endSelectionIndex = e; break; }
            // if 'e' currently is out of character index range OR the current character is an empty space, set the index of that character as 'endSelectionIndex' and break out of this loop immediately
        }
        // -----------------------------------------------------------------------------------------------------------------------------------------------
        
        // Wrap the selected (highlighted) words of 'textField' between start/end tags: "not selected <start> selected text <end> not selected"
        const taggedString = masterScriptString.slice(0, startSelectionIndex) + ' <start> ' + masterScriptString.slice(startSelectionIndex, endSelectionIndex) + ' <end> ' + masterScriptString.slice(endSelectionIndex);

        // convert 'taggedString' (string) into 'taggedArray' (array)
        const taggedArray = SplitString(taggedString);
        const startEndIndexes = [taggedArray.indexOf('<start>'), taggedArray.indexOf('<end>') - 2]; // get the element index of 'taggedArray' where the '<start>' tag exists and the element index of 'taggedArray' where the '<end>' tag exists
        SetSelectedStartEndIndex(startEndIndexes); // set the 'selectedStartEndIndex' hook variable. as the 'startEndIndexes' array. (element 0 = start, element 1 = end)


        if (startEndIndexes[0] === -1) { return; } // If no selected words start index exists, end this function immediately

        var selectedWordsArrays = [ [], [] ]; // will hold both the raw string (element 0) and the ssml taggged (element 1) versions of the selected words

        // --- cycles through all the words (elements) within 'stringScriptArray' & 'ssmlScriptArray' that are between the START index & END index range and add each word (element) to their corresponding 'selectedWordsArrays' groups ---
        for (var i = startEndIndexes[0]; i <= startEndIndexes[1]; i++) {
            selectedWordsArrays[0].push(stringScriptArray[i]); // raw string words
            selectedWordsArrays[1].push(ssmlScriptArray[i]); // ssml tagged words
        }
        // --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------

        SubmitSelectedWordsGroups(selectedWordsArrays); // send 'selectedWordsArrays' to the parent component via 'SubmitSelectedWordsGroups' (use state function)
        UpdateStringScript(stringScriptArray, ssmlScriptArray, startEndIndexes); // update the string script and the master script display to 'stringScriptArray' as a single string (with 'startEndIndexes' as select indexes)
    }
    // ----------------------------------------------------------------------------------------------------------------------------


    // ----- function recieves two array values and updates the string script via 'SetStringScript' & updates the master script display -----
    function UpdateStringScript(stringScriptArray, ssmlScriptArray, startEndSelectIndexes) {

        if (stringScriptArray === undefined || Array.isArray(stringScriptArray) === false || ssmlScriptArray === undefined || Array.isArray(ssmlScriptArray) === false) { console.log('no valid string parameter given'); return; }

        const updatedStringScript = ConvertArrayToString(stringScriptArray); // convert 'stringScriptArray_updated' (array) into 'updatedStringScript' (string)
        SetStringScript(updatedStringScript); // update 'ssmlScript' to 'filteredSSMLScript' via 'SetSSMLScript' (use state function)


        var displayScriptArray = [...stringScriptArray]; // clone 'stringScriptArray' to 'displayScriptArray'

        // ------------------------------------------------ cycle through all word elements in 'ssmlScriptArray' ------------------------------------------------
        for (var i = 0; i < ssmlScriptArray.length; i++) {

            // if the current ssml script word contains a 'prosody' tag, a 'emphasis' tag, and/or a 'break' tag, wrap the cooresponding 'displayScriptArray' word in a 'span' tag and assign it a 'editWord' class
            if (ssmlScriptArray[i].includes('<prosody ') || ssmlScriptArray[i].includes('<emphasis ') || ssmlScriptArray[i].includes('<break ')) { displayScriptArray[i] = '<span class="editWord">' + displayScriptArray[i] + '</span>' }

            // --- if 'startEndSelectIndexes' is defined as an array containing more than 1 element AND its second element is a number greater than 0... ---
            if (startEndSelectIndexes !== undefined && Array.isArray(startEndSelectIndexes) === true && startEndSelectIndexes.length > 1 && startEndSelectIndexes[1] > 0) {

                // if the current ssml script word's array index is the same as the first element in 'startEndSelectIndexes', insert an opening 'span' tag and assign it a 'selectWord' class before the current ssml script word
                if (i === startEndSelectIndexes[0]) { displayScriptArray[i] = '<span class="selectWord">' + displayScriptArray[i] }
                // if the current ssml script word's array index is the same as the second element in 'startEndSelectIndexes', insert a closing 'span' tag after the current ssml script word
                if (i === startEndSelectIndexes[1]) { displayScriptArray[i] = displayScriptArray[i] + '</span>' }
            }
            // ---------------------------------------------------------------------------------------------------------------------------------------------
        }
        // ------------------------------------------------------------------------------------------------------------------------------------------------------

        var updatedDisplayScript = ConvertArrayToString(displayScriptArray) // convert 'displayScriptArray' (array) into 'updatedDisplayScript' (string)
        document.getElementById('scriptInput-masterScript-text').innerHTML = updatedDisplayScript; // get the master script display text element via 'scriptInput-masterScript-text' ID and set its inner HTML as 'updatedDisplayScript'
    }
    // -----------------------------------------------------------------------------------------------------------------------------------


    // ----- function recieves an array value and updates the ssml script via 'SetSSMLScript' & 'SubmitScript' use state functions -----
    function UpdateSSMLScript(ssmlScriptArray) {

        // if 'ssmlScript' is undifined OR not of string type, log a message to the console and end this function immediately
        if (ssmlScriptArray === undefined || Array.isArray(ssmlScriptArray) === false) { console.log('no valid string parameter given'); return; }

        const updatedSSMLScript = ConvertArrayToString(ssmlScriptArray); // convert 'stringScriptArray_updated' (array) into 'updatedStringScript' (string)

        const filteredSSMLScript = updatedSSMLScript.replaceAll('&', 'and'); // replace all undesired sub strings from 'ssmlScript', save the filtered 'ssmlScript' as 'filteredSSMLScript'

        SetSSMLScript(filteredSSMLScript); // update 'ssmlScript' to 'filteredSSMLScript' via 'SetSSMLScript' (use state function)
        SubmitScript(filteredSSMLScript); // send 'filteredSSMLScript' to the parent component via 'SubmitScript' (use state function)
    }
    // ---------------------------------------------------------------------------------------------------------------------------------


    // ------------------------------ function is triggered when the 'Preview' button is clicked ------------------------------
    function PreviewScript(stringToPreview) {

        if ((stringToPreview.trim()).length === 0) return;  // if the number of characters in the 'stringToPreview' string is 0 AFTER all empty and space characters have been removed, end this function at this point

        PlaybackScript(stringToPreview, document.getElementById('previewButton-inputScript')); // trigger the 'PlaybackSpeech' function on the parent component via 'PlaybackScript'

        /*fetch('https://speakeasypro.ca/my-account/d-test')  // --- READ TEXT FILE ---
            .then((response) => {
                return response.json();
            })
            .then((jsonData) => {

                console.log(Object.values(Object.values(jsonData)[0])[0]);
            });*/
    }
    // -------------------------------------------------------------------------------------------------------------------------



    // ------------------------------ function is triggered every time 'triggerClearSSML' is updated ------------------------------
    function ClearSSMLScript() {

        const stringScriptArray_outdated = SplitString(stringScript); // convert 'stringScript' (string) into 'stringScriptArray' (array)
        const ssmlScriptArray_outdated = SplitString(ssmlScript); // convert 'ssmlScript' (string) into 'ssmlScriptArray' (array)

        // cycles through all words in 'stringScriptArray_outdated'. If the current cooresponding 'ssmlScriptArray_outdated' word contains a '<break>' tag, remove the word with the same index from 'stringScriptArray_outdated'
        for (var i = 0; i < stringScriptArray_outdated.length; i++) { if (ssmlScriptArray_outdated[i].includes('<break time="')) { stringScriptArray_outdated.splice(i, 1); } }

        UpdateStringScript(stringScriptArray_outdated, stringScriptArray_outdated); // update the string script and the master script display to 'stringScriptArray_outdated' as a single string
        UpdateSSMLScript(stringScriptArray_outdated); // update the ssml script and the submitted script to 'stringScriptArray_outdated' as a single string
    }
    // --- a current value reference of the 'ClearSSMLScript' function ---
    const ClearSSMLScript_Ref = useRef();
    ClearSSMLScript_Ref.current = ClearSSMLScript;
    // -------------------------------------------------------------------
    // -----------------------------------------------------------------------------------------------------------------------------


    // ------------------------------ function is triggered every time 'returnedEditedText' is updated ------------------------------
    function ReplaceOutdatedScript() {

        // ----------------------------------------------- convert strings to word arrays -----------------------------------------------
        const stringScriptArray_outdated = SplitString(stringScript); // convert 'stringScript' (string) into 'stringScriptArray' (array)
        const ssmlScriptArray_outdated = SplitString(ssmlScript); // convert 'ssmlScript' (string) into 'ssmlScriptArray' (array)
        const ssmlEditedTextArray = SplitString(returnedEditedText); // convert 'returnedEditedText' (string) into 'ssmlEditedTextArray' (array)
        // ------------------------------------------------------------------------------------------------------------------------------
        var stringEditedTextArray = [...ssmlEditedTextArray]; // clone 'ssmlEditedTextArray' to 'stringEditedTextArray'

        // cycles through all words in 'stringEditedTextArray'. If the current word contains a '<break>' tag, replace the current word with an '_'
        for (var i = 0; i < stringEditedTextArray.length; i++) { if (stringEditedTextArray[i].includes('<break time="')) { stringEditedTextArray[i] = '&hArr;'; } }

        // ------------------------------------------------------------- isolate the none selected words of the string & ssml scripts -------------------------------------------------------------
        const stringStart = stringScriptArray_outdated.slice(0, selectedStartEndIndex[0]); // isolate 'stringScriptArray_outdated' from the first word to the start of the current selected word(s)
        const stringEnd = stringScriptArray_outdated.slice(selectedStartEndIndex[1] + 1); // isolate 'stringScriptArray_outdated' from the end of the current selected word(s) to the last word
        const ssmlStart = ssmlScriptArray_outdated.slice(0, selectedStartEndIndex[0]); // isolate 'ssmlScriptArray_outdated' from the first word to the start of the current selected word(s)
        const ssmlEnd = ssmlScriptArray_outdated.slice(selectedStartEndIndex[1] + 1); // isolate 'ssmlScriptArray_outdated' from the end of the current selected word(s) to the last word
        // ----------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------

        // --- assemble 'stringScriptArray_updated': [stringStart] + [stringEditedTextArray] + [stringEnd] ---
        var stringScriptArray_updated = stringStart;
        stringScriptArray_updated.push(...stringEditedTextArray, ...stringEnd);
        // ---------------------------------------------------------------------------------------------

        // ------ assemble 'ssmlScriptArray_updated': [ssmlStart] + [ssmlEditedTextArray] + [ssmlEnd] ------
        const ssmlScriptArray_updated = ssmlStart;
        ssmlScriptArray_updated.push(...ssmlEditedTextArray, ...ssmlEnd);
        // ---------------------------------------------------------------------------------------------

        UpdateStringScript(stringScriptArray_updated, ssmlScriptArray_updated); // update the string script and the master script display to 'stringScriptArray_updated' as a single string
        UpdateSSMLScript(ssmlScriptArray_updated); // update the ssml script and the submitted script to 'ssmlScriptArray_updated' as a single string


        UpdateWordCount(stringScriptArray_updated.length); // update wordCount to the character count of 'stringScriptArray_updated' via 'UpdateWordCount' (use state function)
        SetEnableEditing(ConvertArrayToString(stringScriptArray_updated) === ''); // update enableEditing based of IF 'updatedStringScript' is empty as a string ('' = true) via 'SetEnableEditing' (use state function)
        ResetEditedText(null); // send a null value to the parent component via 'ResetEditedText' global reference to a (use state function)
    }
    // --- a current value reference of the 'ReplaceOutdatedScript' function ---
    const ReplaceOutdatedScript_Ref = useRef();
    ReplaceOutdatedScript_Ref.current = ReplaceOutdatedScript;
    // -------------------------------------------------------------------------
    // -------------------------------------------------------------------------------------------------------------------------------


    // ------------------------------ function is triggered every time  'updatedSelectedWords' is updated ------------------------------
    function AssembleSSMLScript() {

        // If the 'ssmlSCript' variable is an empty string, end this function immediately
        if (ssmlScript === '') return;

        const stringScriptArray = SplitString(stringScript); // convert 'stringScript' (string) into 'stringScriptArray' (array)
        const ssmlScriptArray = SplitString(ssmlScript); // convert 'ssmlScript' (string) into 'ssmlScriptArray' (array)


        // --- cycles through all the words (elements) within 'ssmlScriptArray' that are between the START index & END index range and replace each word (element) with their corresponding word in 'updatedSelectedWords' ---
        var selectedWordsIndex = 0;
        for (var i = selectedStartEndIndex[0]; i <= selectedStartEndIndex[1]; i++) {
            ssmlScriptArray[i] = updatedSelectedWords[selectedWordsIndex];
            selectedWordsIndex++;
        }
        // -------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------


        UpdateStringScript(stringScriptArray, ssmlScriptArray, selectedStartEndIndex); // update the string script and the master script display to 'stringScriptArray' as a single string (with 'selectedStartEndIndex') as select indexes)
        UpdateSSMLScript(ssmlScriptArray); // update the ssml script and the submitted script to 'ssmlScriptArray' as a single string
    }
    // --- a current value reference of the 'AssembleSSMLScript' function ---
    const AssembleSSMLScript_Ref = useRef();
    AssembleSSMLScript_Ref.current = AssembleSSMLScript;
    // ----------------------------------------------------------------------
    // ---------------------------------------------------------------------------------------------------------------------------------



    return (

        <Box id="boxWrapper-masterScriptView" className="sectionWrapper">



            <Grid className="sectionHeading" container> {/* --------------------------------- MODULE TITLE CONTAINER  --------------------------------- */}

                <Grid item xs={1}>

                    {/* -------------------------- modify script button -------------------------- */}
                    <Button
                        id="editScriptButton-inputScript"
                        className="button smallButton"
                        variant="contained"
                        disabled={!enableEditing}

                        onClick={() => { SubmitEditSelection(); }}

                    > Modify </Button>
                    {/* ------------------------------------------------------------------------ */}

                </Grid>

                <Grid item xs={10}>

                    {/* -------------------- module title -------------------- */}
                    <h4>Master Script View</h4>
                    {/* ------------------------------------------------------ */}

                </Grid>

                <Grid item xs={1}>

                    {/* -------------------- preview script button -------------------- */}
                    <Button
                        id="previewButton-inputScript"
                        className="previewButton button smallButton"
                        variant="contained"
                        disabled={stringScript.length === 0} // disable this button if no script is applied

                        onClick={() => { PreviewScript(ssmlScript); }}
                    > Preview </Button>
                    {/* --------------------------------------------------------------- */}

                </Grid>


            </Grid> {/* -------------------------------------- MODULE TITLE CONTAINER  -------------------------------------- */}

            {/* --------------------------------- MASTER SCRIPT DISPLAY  --------------------------------- */}

            <div id='scriptInput-masterScript'>
                <Box
                    id="scriptInput-masterScript-text"

                    onMouseDown={() => { 
                        document.getElementById('scriptInput-masterScript-text').innerHTML = stringScript; // set the master script display text's inner HTML as 'stringScript'
                        ClearSelection(); // clear 'receivedCurrentWordsGroups' in the parent component
                    }}
                    onMouseUp={() => GetSelectionWords(document.getElementById('scriptInput-masterScript-text'))}
                ></Box> {/* ------------------------------------ MASTER SCRIPT DISPLAY ------------------------------------ */}
                </div>

            {/*<TextField
                id="scriptTextField-inputScript"
                fullWidth
                multiline
                rows={9}
                placeholder="Click the 'MODIFY' button to apply your script"
                inputProps={{ readOnly: true }}

                onMouseDown={ClearSelection}
                onMouseUp={(e) => GetSelectionWords(e.target)}
            />*/} 

            <Grid container id='sectionFooter-inputScript'> {/* --------------------------------- MODULE FOOTER CONTAINER  --------------------------------- */}

                {/* --------------------------- script word count display --------------------------- */}
                <Grid item xs={6} id="wordCount-inputScript"> Word Count: {wordCount} words </Grid>
                {/* --------------------------------------------------------------------------------- */}

                {/* --------------------------- script approximate length display --------------------------- */}
                <Grid item xs={6} id="scriptLength-inputScript"> Approximate Script Length: {scriptPlaybackLength}</Grid>
                {/* ----------------------------------------------------------------------------------------- */}

            </Grid> {/* ------------------------------------- MODULE FOOTER CONTAINER  ------------------------------------- */}



        </Box>
    )
}