import './SelectMenu.css';
import { useState, useEffect, useRef } from 'react';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';

export default function SelectMenu({ reset, enabled, options, defaultText, GetSelectionValue }) {

    const [selection, SetSelection] = useState(''); // stores the currently selected 'MenuItem' value in 'selection'. The 'SetSelection' function is triggered every time the a new 'MenuItem' is selected


    useEffect(() => { ResetMenu_Ref.current(); }, [reset, enabled]) // triggers the 'ResetMenu_Ref' function every time 'reset' or 'enabled' changes


    // Clear the Select Menu selected 'MenuItem' and its 'MenuItem' options
    function ResetMenu() {

        SetSelection('');
        GetSelectionValue('');
        options = null;
    }
    // --------------------------------------------------------------------
    // --- a current value reference of the 'ResetMenu' function ---
    const ResetMenu_Ref = useRef();
    ResetMenu_Ref.current = ResetMenu;
    // -------------------------------------------------------------



    return (



        <FormControl className='selectMenu-form'>

            <Select
                className='selectMenu-select'
                displayEmpty
                value={selection}
                disabled={!enabled}
                
                onChange={(e) => [SetSelection(e.target.value), GetSelectionValue(e.target.value)]}
            >

                <MenuItem value="">{defaultText}</MenuItem>

                {options}
                
            </Select>
        </FormControl>



    )
}