import { useState, useEffect, useRef } from 'react';
import './VerticalSlider.css';
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';

export default function VerticalSlider({ enabled, sliderIndex, numberOfSteps, jumpToStep, SubmitCurrentValue }) {

    useEffect(() => { SetCurrentValue(jumpToStep); }, [jumpToStep]) // triggers the 'SetCurrentValue' function every time 'jumpToStep' changes

    // --- a current value reference of the 'sliderIndex' variable ---
    const sliderIndex_Ref = useRef();
    sliderIndex_Ref.current = sliderIndex;
    // ---------------------------------------------------------------

    // --- a current value reference of the 'SubmitCurrentValue' function ---
    const SubmitCurrentValue_Ref = useRef();
    SubmitCurrentValue_Ref.current = SubmitCurrentValue;
    // ----------------------------------------------------------------------


    const [currentValue, SetCurrentValue] = useState(2); // stores the current slider value in 'currentValue'. The 'SetCurrentValue' function is triggered every time the slider value changes
    useEffect(() => { SubmitCurrentValue_Ref.current(sliderIndex_Ref.current, currentValue); }, [currentValue]) // triggers the 'SubmitCurrentValue_Ref' function every time 'currentValue' changes


    // prevents the default behaviour of this selection slider when the left or right arow keyboard keys are pressed down. function is triggered every time a keyboard key is pressed down while focusing on this selection slider
    function preventHorizontalKeyboardNavigation(event) { 
        if (event.key === 'ArrowLeft' || event.key === 'ArrowRight') { // If the left or right arow keyboard keys are pressed down...
            event.preventDefault(); //prevents the default behaviour of this selection slider when focused
        }
    }
    // ---------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------



    return (



        <Box sx={{ height: 100 }}> 

            {/* -------------------- selection slider -------------------- */}
            <Slider className='slider'
                sx={{
                    '& input[type="range"]': {
                        WebkitAppearance: 'slider-vertical',
                    },
                }}
                orientation="vertical"
                disabled={!enabled}
                step={1}
                marks
                min={0}
                max={numberOfSteps -1}
                value={currentValue}



                onChange={(e) => SetCurrentValue(e.target.value)}

                onKeyDown={preventHorizontalKeyboardNavigation}
            />
            {/* ---------------------------------------------------------- */}

        </Box>



    )
}